<template lang="pug">
    AuthLayout(:title="title" :more="more" :disable="form.disable")
        template(#form)
            v-form(ref="form" lazy-validation)
                v-text-field(
                    :label="email"
                    prepend-icon="mdi-email"
                    append-icon="mdi-close"
                    v-model="form.email"
                    type="email"
                    :rules="emailRules"
                    @click:append="form.email = null"
                    :disabled="form.disable"
                    required
                )
                v-text-field(
                    :label="password"
                    prepend-icon="mdi-lock"
                    :append-icon="showPassword ? 'mdi-eye-off' : 'mdi-eye'"
                    v-model="form.password"
                    :type="showPassword ? 'text' : 'password'"
                    :rules="passwordRules"
                    @click:append="showPassword = !showPassword"
                    :disabled="form.disable"
                    required
                )
                v-checkbox(
                    :label="remember"
                    v-model="form.remember"
                    color="green darken-1"
                    :disabled="form.disable"
                ).v-input-no-message
                    template(#label)
                        v-tooltip(top)
                            template(#activator="{ on }")
                                span(v-on="on").remember-tooltip {{ remember }}
                            span {{ rememberTooltip }}
        template(#action)
            v-btn(
                block
                :loading="form.loading"
                :disabled="form.disable"
                color="blue darken-1"
                @click="submit"
            ).mx-2.mb-2.white--text {{ login }}
        template(#app)
            Snackbar(ref="snackbar")
</template>

<script>
    import AuthLayout from '@/components/auth/AuthLayout.vue'
    import Snackbar from '@/components/common/Snackbar.vue'
    export default {
        name: 'AuthLogin',
        components: {
            AuthLayout, Snackbar
        },
        data() {
            return {
                title: '登录账户',
                email: '* 邮箱',
                emailRules: [
                    v => !!v || '请输入账户邮箱!',
                    v => /^([A-Za-z0-9_\-]{3,})@(([A-Za-z0-9_\-]+)\.([A-Za-z]{2,})(\.([A-Za-z]{2,}))?)$/.test(v) || '无效的邮箱格式!'
                ],
                password: '* 密码',
                passwordRules: [
                    v => !!v || '请输入账户密码!',
                    v => v && v.length >= 6 || '密码长度不能小于 6!',
                ],
                showPassword: false,
                login: '登录',
                remember: '保持登录会话?',
                rememberTooltip: '如果这不是您的私人设备，不建议选中！',
                more: {
                    text: '更多选项',
                    items: [
                        { title: '注册账户?', to: '/register' },
                        { title: '忘记密码?', to: '/forget' }
                    ],
                },
                form: {
                    email: null,
                    password: null,
                    remember: false,
                    disable: false,
                    loading: false
                }
            }
        },
        methods: {
            async submit (evt) {
                evt.preventDefault();
                if (this.$refs.form.validate()) {
                    this.form.disable = true;
                    this.form.loading = true;
                    this.$store.dispatch('login', {
                        email: this.form.email,
                        password: this.form.password,
                        remember: this.form.remember
                    })
                        .then(() => {
                            this.$refs.snackbar.show('success', '登录成功! 欢迎回来~', () => {
                                let redirect = this.$route.query.redirect;
                                if (!redirect || redirect === '/login')
                                    redirect = '/user';
                                this.form.loading = false;
                                this.$router.push(redirect);
                            });
                        })
                        .catch(err => {
                            let msg = err.message;
                            if (err.response && err.response.data.msg)
                                msg = err.response.data.msg;
                            this.$refs.snackbar.show('error', '登录失败: ' + msg);
                            this.form.disable = false;
                            this.form.loading = false;
                        })
                }
            }
        }
    }
</script>

<style scoped lang="stylus">
    .remember-tooltip
        cursor help
        border-bottom 1px dotted black;
</style>
